import { useState, useEffect } from "react";
import "./styles.css";

export default function App() {
  const [base64String, setBase64String] = useState("");

  const sayHello = (e) => {
    const { files } = e.target;
    console.log(files[0].size);
    base64Encode(files[0]);
  };

  useEffect(() => {
    if (base64String) {
      const string = base64String.split(",")[1];
      console.log(string.slice(string.length - 2, string.length));
      let x =
        string.length * (3 / 4) -
        (string.slice(string.length - 2, string.length) !== "=="
          ? string.slice(string.length - 1, string.length) === "="
            ? 1
            : 0
          : 2);
      console.log(x);
    }
  }, [base64String]);

  function base64Encode(file) {
    var reader = new FileReader();
    if (file) {
      reader.readAsDataURL(file);
      reader.onload = () => {
        setBase64String(reader.result);
      };
      reader.onerror = () => {
        alert("Error Occur");
      };
    }
  }
  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>Start editing to see some magic happen!</h2>

      <input type="file" name="hello" onChange={sayHello} />
    </div>
  );
}
